import React, { Component } from "react";
import "./home.css";
import Header from "../../components/header/header";
import Slider from "react-slick";
import Rehat from "../../assets/images/rehat.png";
import Kesbek from "../../assets/images/kesbek.png";
import Covid from "../../assets/images/covid.png";
import Footer from "../../components/footer/footer";
import Button from "../../components/button/button";

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      changeHeader: false,
      githubAPI: {
        name: "",
      },
    };
  }

  getData_API = () => {
    const githubApi = fetch("https://api.github.com/users/firmnardians");
    githubApi
      .then((res) => {
        return res.json();
      })
      .then((data) => {
        console.log(data);
        this.setState({
          githubAPI: {
            name: data.login,
          },
        });
      })
      .catch((err) => {
        console.log(err);
      });
  };

  listenScrollEvent = (e) => {
    if (window.scrollY > 10) {
      this.setState({
        changeHeader: false,
      });
    } else {
      this.setState({
        changeHeader: true,
      });
    }
  };

  componentDidMount() {
    this.getData_API();
    window.addEventListener("scroll", this.listenScrollEvent);
  }

  render() {
    return (
      <>
        <div className="container-fixed">
          <Header
            changeHeader={this.state.changeHeader}
            name={this.state.githubAPI.name}
          />
          <div className="home-prefix">
            <div className="impact-text">
              <h4>I'am create apps impact for everyone.</h4>
            </div>
            <div></div>
          </div>
        </div>
        <div className="fakebar"></div>

        <div className="container-app">
          <div className="home-app">
            <div className="grid">
              <div className="col">
                <div className="card">
                  <div className="data-app">
                    <div className="download-title">
                      <h2>Temukan aplikasi yang kamu sukai</h2>
                      <p>
                        Aplikasi dapat mengubah cara Anda melakukan apa pun yang
                        Anda sukai.
                      </p>
                    </div>
                  </div>
                </div>
              </div>
              <div className="col">
                <div className="card">
                  <div className="data-slick">
                    <Slider {...settings}>
                      <div className="container-slick">
                        <div className="card-slider">
                          <div className="data-slider-content">
                            <img className="images-slider" src={Rehat} alt="" />
                          </div>
                          <div className="card-title-slider">
                            <h3>REHAT</h3>
                            <div className="download-btn">Download</div>
                          </div>
                        </div>
                      </div>

                      <div className="container-slick">
                        <div className="card-slider">
                          <div className="data-slider-content">
                            <img
                              className="images-slider"
                              src={Kesbek}
                              alt=""
                            />
                          </div>
                          <div className="card-title-slider">
                            <h3>KESBEK</h3>
                            <div className="download-btn">Download</div>
                          </div>
                        </div>
                      </div>

                      <div className="container-slick">
                        <div className="card-slider">
                          <div className="data-slider-content">
                            <img className="images-slider" src={Covid} alt="" />
                          </div>
                          <div className="card-title-slider">
                            <h3>COVID ID</h3>
                            <div className="download-btn">Download</div>
                          </div>
                        </div>
                      </div>
                    </Slider>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="container-react">
          <div className="slide">
            <div className="hero-slider"></div>
          </div>

          <div className="card-center">
            <div className="title-build">
              <h3>Get in Touch</h3>
              <p>
                If you have a project that you want to get started, think you
                need my help with something or just want to have a cup of
                coffee/tea with me, then drop me a mail at{" "}
                <a href="mailto:name@email.com">firmnardians@gmail.com</a> or
                click the button below.
              </p>
              <Button />
            </div>
            <div className="icon-social">
              <a href="https://dribbble.com/firmnardians">
                <div className="dribbble"></div>
              </a>
              <a href="http://github.com/firmnardians">
                <div className="github"></div>
              </a>
              <a href="https://twitter.com/firmnardians">
                <div className="twitter"></div>
              </a>
            </div>
          </div>
        </div>

        <Footer />
      </>
    );
  }
}

const settings = {
  dots: true,
  infinite: false,
  speed: 500,
  slidesToShow: 2,
  slidesToScroll: 2,
  initialSlide: 0,
  responsive: [
    {
      breakpoint: 1024,
      settings: {
        slidesToShow: 2,
        slidesToScroll: 2,
      },
    },
    {
      breakpoint: 1010,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1,
      },
    },
    {
      breakpoint: 480,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1,
      },
    },
  ],
};

export default Home;
