import React from "react";
import "./header.css";

const Header = (props) => {
  return (
    <div className={props.changeHeader ? "header black" : "header white"}>
      <div className="header-content">
        <div className="header-brand">
          <h3 className={props.changeHeader ? "text-white" : "text-black"}>
            {props.name}
          </h3>
        </div>
      </div>
    </div>
  );
};

export default Header;
